<script>

import {ref} from 'vue'
//import env from '@/env.js'
export default {
  name: 'HomeView',
  components: {
  },
  setup (){
    const search=ref("");
    const movies=ref([]);
    const SearchMovies=()=>{
       if(search.value !=""){
         fetch(`https://jsonplaceholder.typicode.com/photos`)
          .then(response => response.json())
          .then(data => {
            console.log(data)
            movies.value = data;
            search.value = "";
          }).catch(err => console.log(" error found"+err.message));
       } 
    }
    return{search,movies,SearchMovies}
  }
}
</script>
