<template>
  <div class="movie-detail">
    <h2>{{movie.title}}</h2>
    <p>{{ movie.thumbnailUrl }}</p>
    <img :src="movie.url" alt="Movie Poster" class="featured-img" />
    <p>{{ movie.albumId }}</p>
  </div>
</template>

<script>
import { ref, onBeforeMount } from 'vue';
import { useRoute } from 'vue-router';
//import env from '@/env.js';
export default {
setup () {
    const movie = ref({});
    const route = useRoute();
    onBeforeMount(() => {
      fetch(`https://jsonplaceholder.typicode.com/photos/${route.params.id}`)
        .then(response => response.json())
        .then(data => {
          movie.value = data;
          console.log(data)
        });
    });
    return {
      movie
    }
  }
}
</script>

<style lang="scss">
.movie-detail {
  padding: 16px;
  h2 {
    color: #FFF;
    font-size: 28px;
    font-weight: 600;
    margin-bottom: 16px;
  }
  .featured-img {
    display: block;
    max-width: 200px;
    margin-bottom: 16px;
  }
  p {
    color: #FFF;
    font-size: 18px;
    line-height: 1.4;
  }
}
</style>